'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Lapangan } from '@/types';
import LapanganGrid from '@/components/admin/lapangan/LapanganGrid';
import LapanganFormDialog from '@/components/admin/lapangan/LapanganFormDialog';

export default function LapanganPage() {
    const [lapangans, setLapangans] = useState<Lapangan[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<Lapangan | null>(null);

    const fetchLapangans = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/lapangans');
            setLapangans(response.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat data lapangan.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchLapangans(); }, []);

    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: Lapangan) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Hapus lapangan ini? Data booking terkait mungkin akan error.")) return;
        try {
            await axios.delete(`/api/lapangans/${id}`);
            toast.success("Lapangan berhasil dihapus.");
            fetchLapangans();
        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menghapus.");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                await axios.put(`/api/lapangans/${editData.id}`, formData);
                
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/lapangans/${editData.id}/foto`, imgData);
                }
                toast.success("Lapangan diperbarui!");
            } else {
                const data = new FormData();
                Object.keys(formData).forEach(key => {
                    if (key === 'status_aktif') {
                        data.append(key, formData[key] ? '1' : '0');
                    } else if (formData[key] !== null && formData[key] !== undefined) {
                        data.append(key, formData[key]);
                    }
                });
                
                await axios.post('/api/lapangans', data);
                toast.success("Lapangan ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchLapangans();
        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Gagal menyimpan.");
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Lapangan</h1>
                    <p className="text-gray-500 text-sm">Kelola fasilitas, harga, dan jam operasional.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Lapangan
                </Button>
            </div>

            <LapanganGrid 
                loading={loading}
                lapangans={lapangans}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <LapanganFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
            />
        </div>
    );
}